import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Row, Col, Button, message, Pagination, Popconfirm } from 'antd';

class Draft extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            draftList: [],
            current: 1,
            total: 0
        };
        ['goToEdit', 'deleteDraft', 'changePage'].forEach(m=>this[m] = this[m].bind(this))

    }
    
    categoryText(type) {
        switch(type) {
            case "1":
                return "债权";
            case "2":
                return "股权";
            case "3":
                return "资产收购";
            case "4":
                return"资产转让";
        }
    }
    goToEdit(id, category) {
        let type;
        switch(category.toString()) {
            case "1":
                type = "debts";
                break;
            case "2":
                type = "stock";
                break;
            case "3":
                type = "purchase";
                break;
            case "4":
                type = "transfer";
                break;
        }
        hashHistory.push(`/fund/space/publish/${type}?id=${id}`)
    }

    deleteDraft(id) {
        let that = this;
        ajax({
            url: "/fund/draft/delete",
            data: {id},
            callback(data) {
                message.success("删除成功！", 1, () => {
                    that.renderPage(1);
                })
            }
        })
    }

    renderPage(page) {
        let that = this;
        ajax({
            url: "/fund/draft/getInfoList",
            data: {page},
            callback(data) {
                that.setState({ 
                    draftList: data.infoList,
                    total: data.total
                });
            }
        })
    }

    componentWillMount() {
        this.renderPage(1);
    }

    changePage(page) {
        this.renderPage(page)
    }

    render() {
        return (
            <Row className="draftFrame">
                <Row className="menuTitle">资金草稿</Row>
                <Row className="draftList">
                    {this.state.draftList.map(item => 
                        <Row className="each" key={item.id}>
                            <Col span={18} style={{borderRight: "1px dashed #e1e1e1"}}>
                                <Row className="fundTitle">{item.title}</Row>
                                <Row>
                                    <Col span={6}>
                                        <Row className="item">
                                            投资类别:<span className="redText content">{this.categoryText(item.category)}</span>
                                        </Row>
                                        <Row className="item">
                                            投资地区:<span className="content">{item.region.join(',')}</span>
                                        </Row>
                                    </Col>
                                    <Col span={9}>
                                        <Row className="item">
                                            投资类型:<span className="redText content">{item.type.join(",")}</span>
                                        </Row>
                                        <Row className="item">
                                            单笔投资区间:<span className="redText content">{item.investMin}万元~{item.investMax}万元</span>
                                        </Row>
                                    </Col>
                                    <Col span={9}>
                                        <Row className="item">
                                            投资行业:<span className="content">{item.industry.join(",")}</span>
                                        </Row>
                                        <Row className="item">
                                            已查看人数:<span className="content">{item.seen}人</span>
                                        </Row>
                                    </Col>
                                </Row>
                            </Col>
                            <Col span={6}>
                                <Row type="flex" justify="center" style={{marginTop: 30}}>
                                    <Col span={9} offset={2}>
                                        <Button type="primary" onClick={this.goToEdit.bind(this, `${item.id}`, `${item.category}`)}>编辑</Button>
                                    </Col>
                                    <Col span={9} offset={2}>
                                        <Popconfirm title="是否确认删除？" onConfirm={this.deleteDraft.bind(this, `${item.id}`)} okText="是" cancelText="否">
                                            <Button type="danger">删除</Button>
                                        </Popconfirm>
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                    )}
                </Row>
                <Row type="flex" justify="center" align="middle">
                    <Pagination
                        defaultCurrent={this.state.current}
                        total={this.state.total}
                        onChange={this.changePage}
                    />
                </Row>
            </Row>
        )
    }
}

module.exports = Draft;


